@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@200;300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@100;300&display=swap);
@import url(https://fonts.googleapis.com/css?family=Playfair+Display:400,900|Poppins:400,500);
:root {
    --font-black: rgba(0, 0, 0, 0.767);
    --font-blue: #b4ddff;
  }

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Raleway', sans-serif;
    color: rgba(0, 0, 0, 0.767);
    color: var(--font-black);
}

.center {
    margin: 0 auto;
    text-align: center;
}

a {
    text-decoration: none;
    color: black;
}

.background-image {
    background-image: url(/memovie/static/media/film.8eef8843.jpg);
    background-size: cover;
    height: 100vh;
    overflow-y: scroll;
}

.btn {
    border: none;
    border-radius: 4px;
    background: none;
    text-decoration: none;
    padding: 0.5rem 1rem;
    outline: none;
    color: black;
    font-size: 1.5rem;
    cursor: pointer;
    font-family: 'Raleway', sans-serif;
    transition: all 0.1s linear;
    margin: 0 auto;
}

.btn:hover {
    background:rgba(0, 0, 0, 0.05);
}

.btn-secondary {
    color: #b4ddff;
    background-color: rgba(0, 0, 0, 0.5);
}

.btn-secondary:hover {
    background-color: rgba(0, 0, 0, 0.7);
}

.paper {
    padding: 1rem;
}

.flex-align-center {
    display: flex; 
    align-items: center;
}

.mt-1 {
    margin-top: 1rem;
}

.mr-1 {
    margin-right: 1rem;
}

.mb-1 {
    margin-bottom: 1rem;
}

.ml-1 {
    margin-left: 1rem;
}

@media only screen and (max-width: 650px) {
    .btn {
        font-size: 1.2rem;
    }

    .paper {
        padding: 1rem;
    }
}
.dshbrd__container {
    position: relative;
    width: 100vw;
    height: 65vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.dshbrd__title-container {
    display: flex;
}

.dshbrd__heading {
    transform: translateY(-50px);
    opacity: 0;
    font-size: 10rem;
    font-weight: 100;
    margin: 0;
}

.dshbrd__heading-blue {
    transform: translateY(-50px);
    opacity: 0;
    color: var(--font-blue);
}

.dshbrd__welcome {
    font-size: 1.5rem;
    margin-bottom: 0.8rem;
    color: maroon;
}

.dshbrd__welcome span {
    transform: translateX(-20px);
    opacity: 0;
}

.dshbrd__welcome span:last-child {
    opacity: 0;
}

.dshbrd__btn-group {
    width: 23rem;
    display: flex; 
    justify-content: space-between;
}

@media only screen and (max-width: 750px) {
    .dshbrd__heading {
        font-size: 7rem;
    }

    .dshbrd__sub-heading {
        font-size: 1.5rem;
    }
}

@media only screen and (max-width: 500px) {
    .dshbrd__heading {
        font-size: 4.5rem;
    }

    .dshbrd__heading {
        margin-bottom: 0.4rem;
    }

    .dshbrd__sub-heading {
        font-size: 1.2rem;
    }

    .dshbrd__btn-group {
        width: 80%;
    }

    .dshbrd__btn {
        padding: 0.4rem;
        font-size: 1.1rem;
    }
}
.header {
    width: 100%;
    padding: 2rem 0 1.5rem;
    display: flex;
    align-items: center;
}

.header__icon-container {
    width: 33%;
    display: flex;
    align-items: center;
}

.header__icon-home {
    color: black;
    margin-right: 1rem;
}

.header__h1 {
    font-size: 3.7rem;
    font-weight: 380;
    width: 33%;
    text-align: center;
}

.header__nav {
    justify-self: end;
    display: flex;
    align-items: center;
    width: 33%;
    justify-self: flex-end;
}

.header__nav > a, .header__nav > button, .header__logout {
    font-size: 1.3rem;
    padding: 0.5rem;
}

.header__logout {
    margin: 0;
}

.fa-circle {
    font-size: 0.1rem;
}

@media only screen and (max-width: 1200px) {
    .header__h1 {
        font-size: 2.9rem;
    }
}

@media only screen and (max-width: 1150px) {
    .header__nav > a, .header__nav > button, .header__logout {
        font-size: 1.2rem;
    }
}

@media only screen and (max-width: 1010px) {
    .header__icon-home {
        font-size: 2rem;
    }

    .header__nav > a, .header__nav > button, .header__logout {
        font-size: 1.1rem;
        padding-left: 0.2rem;
        padding-right: 0.2rem;
    }
}

@media only screen and (max-width: 870px) {
    .header {
        grid-template-columns: repeat(3, 1fr);
        display: grid;
        grid-template-areas: 
        "emblem   h1   h1"
        "navlinks navlinks navlinks";
        grid-row-gap: 0.5rem;
        justify-items: stretch;
    }

    .header__icon-home {
        grid-area: emblem;
    }

    .header__h1 {
        width: 100%;
        text-align: right;
        grid-area: h1;
    }

    .header__nav {
        width: 100%;
        border-top: 1px solid black;
        padding-top: 0.5rem;
        grid-area: navlinks;
        justify-self: stretch;
        display: flex;
        justify-content: space-between;
    }

    .header__nav > a, .header__nav > button, .header__logout {
        margin: 0;
        padding: 0.5rem;
        font-size: 1.5rem;
    }

    .fa-circle {
        display: none;
    }
}

@media only screen and (max-width: 500px) {
    .header__h1 {
        font-size: 2rem;
    }

    .header__nav > a, .header__nav > button, .header__logout {
        font-size: 1.1rem;
    }

    .fa-circle {
        display: block;
    }

    .header__nav > a, .header__nav > button, .header__logout {
        padding: 0.2rem 0.4rem;
    }
}
.movie-table__container {
    margin: 1rem auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow-x: auto;
    padding-bottom: 1rem;
}

.movie-table__btn {
    margin: 0.1rem auto;
}

.movie-table__h2 {
    margin-top: 2.5rem;
}
.mf__form {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    width: 25rem;
}

.mf__h1 {
    margin: 0 auto 0.8rem;
}

.mf__title__input {
    position: relative;
}

.mf-asterisk {
    position: absolute;
    right: -10px;
    font-size: 8px;
}

.mf__small {
    text-align: center;
    margin-bottom: 0.7rem;
}

@media only screen and (max-width: 650px) {
    .mf__form {
        width: 100%;
    }
}

@media only screen and (max-width: 500px) {
    .mf__paper {
        padding: 1rem;
    }
}
.dialogue__container {
    padding: 1rem;
}

.dialogue__heading {
    margin-bottom: 0.5rem;
    color: var(--font-black);
    text-align: center;
}

.dialogue__content-alert {
    font-family: 'Raleway';
    font-size: 1.1rem;
    color: rgb(102, 60, 0);
}

.dialogue__content-alert > p {
    margin-left: 0.4rem;
}

.no-movies__h3 {
    margin: 0;
    margin-bottom: 0.7rem;
}

.picked-movie__h1 {
    font-size: 3.5rem;
    color: var(--font-black);
    margin: 0;
    margin-bottom: 1rem;
    text-align: center;
    word-break: break-word;
}

.picked-movie__director {
    text-align: center;
    font-size: 1.4rem;
}

@media only screen and (max-width: 660px) {
    .dialogue__container {
        padding: 1rem 0.5rem;
    }
    
    .picked-movie__h1 {
        font-size: 1.5rem;
    }

    .picked-movie__director {
        font-size: 1rem;
        padding-bottom: 0.5rem;
    }

}
.mp__h3 {
    font-size: 1.6rem;
    margin-bottom: 1.5rem;
}

.filters__container {
    padding: 1rem;
    overflow-x: scroll;
}

.filters {
    display: flex;
    justify-content: space-between;
    width: 80%; 
    margin: 0 auto;
}

.filters__group {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.filters__group-watched-before {
    white-space: nowrap;
}

.filters__heading {
    margin-bottom: 0.8rem;
    font-family: "Roboto", arial;
    font-size: 1.1rem;
}

.filters__chevron {
    cursor: pointer;
    border-radius: 2px;
    margin-left: 0.2rem;
}

.filters__chevron:hover {
    background: rgba(221, 217, 212, 0.4);
}

@media only screen and (max-width: 900px) {
    .filters__container {
        padding: 1rem 0;
    }

    .mp__h3 {
        font-size: 1.5rem;
    }

    .filters__group {
        margin: 0 1rem;
        text-align: center;
    }

    .filters__group:first-child {
        margin-left: 0;
    }

    .filters__heading {
        font-size: 1rem;
    }

    .filters__watched-checkbox {
        margin-right: 0.7rem;
    }
}

@media only screen and (max-width: 560px) {
    .filters__group-watched-before {
        padding-right: 2.5rem;
    }
}
.authform__container {
