.mp__h3 {
    font-size: 1.6rem;
    margin-bottom: 1.5rem;
}

.filters__container {
    padding: 1rem;
    overflow-x: scroll;
}

.filters {
    display: flex;
    justify-content: space-between;
    width: 80%; 
    margin: 0 auto;
}

.filters__group {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.filters__group-watched-before {
    white-space: nowrap;
}

.filters__heading {
    margin-bottom: 0.8rem;
    font-family: "Roboto", arial;
    font-size: 1.1rem;
}

.filters__chevron {
    cursor: pointer;
    border-radius: 2px;
    margin-left: 0.2rem;
}

.filters__chevron:hover {
    background: rgba(221, 217, 212, 0.4);
}

@media only screen and (max-width: 900px) {
    .filters__container {
        padding: 1rem 0;
    }

    .mp__h3 {
        font-size: 1.5rem;
    }

    .filters__group {
        margin: 0 1rem;
        text-align: center;
    }

    .filters__group:first-child {
        margin-left: 0;
    }

    .filters__heading {
        font-size: 1rem;
    }

    .filters__watched-checkbox {
        margin-right: 0.7rem;
    }
}

@media only screen and (max-width: 560px) {
    .filters__group-watched-before {
        padding-right: 2.5rem;
    }
}